<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础使用1">基础使用</a></li><li><a href="#vue-doc-自定义1">自定义</a></li><li><a href="#vue-doc-异步关闭1">异步关闭</a></li><li><a href="#vue-doc-禁用关闭1">禁用关闭</a></li><li><a href="#vue-doc-自定义位置1">自定义位置</a></li><li><a href="#vue-doc-实例化基本用法1">实例化-基本用法</a></li><li><a href="#vue-doc-实例化确认对话框1">实例化-确认对话框</a></li><li><a href="#vue-doc-实例化自定义内容1">实例化-自定义内容</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-modal对话框1" class="vue-doc-element">Modal 对话框</h1>
<p class="vue-doc-element">模态对话框，在浮层中显示，引导用户进行相关操作。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-buttonsize1" class="vue-doc-element">Button Size</h1>
<p class="vue-doc-element">Button</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<democustom></democustom>
<demoasync></demoasync>
<demodisabled></demodisabled>
<demovertical></demovertical>
<demoinstance></demoinstance>
<democonfirm></democonfirm>
<demorender></demorender>
        </div>


        <div class="vue-doc-content">
          <p class="vue-doc-element"><code class="vue-doc-element">Modal</code>提供了两种用法，普通组件使用和封装好的简洁实例调用。</p>
<h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-modalprops1_1" class="vue-doc-element">Modal props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>对话框是否显示，可使用 v-model 双向绑定数据。</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>title</td>
<td>对话框标题，如果使用 slot 自定义了页头，则 title 无效</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>closable</td>
<td>是否显示右上角的关闭按钮，关闭后 Esc 按键也将关闭</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>mask-closable</td>
<td>是否允许点击遮罩层关闭</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>loading</td>
<td>点击确定按钮时，确定按钮是否显示 loading 状态，开启则需手动设置visible来关闭对话框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>scrollable</td>
<td>页面是否可以滚动</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>fullscreen</td>
<td>是否全屏显示</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>draggable</td>
<td>是否可以拖拽移动</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>mask</td>
<td>是否显示遮罩层，开启 draggable 时，强制不显示</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>ok-text</td>
<td>确定按钮文字</td>
<td>String</td>
<td>确定</td>
</tr>
<tr>
<td>cancel-text</td>
<td>取消按钮文字</td>
<td>String</td>
<td>取消</td>
</tr>
<tr>
<td>width</td>
<td>对话框宽度，对话框的宽度是响应式的，当屏幕尺寸小于 768px 时，宽度会变为自动auto。当其值不大于 100 时以百分比显示，大于 100 时为像素</td>
<td>Number / String</td>
<td>520</td>
</tr>
<tr>
<td>footer-hide</td>
<td>不显示底部</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>styles</td>
<td>设置浮层样式，调整浮层位置等，该属性设置的是.ivu-modal的样式</td>
<td>Object</td>
<td>-</td>
</tr>
<tr>
<td>class-name</td>
<td>设置对话框容器.ivu-modal-wrap的类名，可辅助实现垂直居中等自定义效果</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>z-index</td>
<td>层级</td>
<td>Number</td>
<td>1000</td>
</tr>
<tr>
<td>transition-names</td>
<td>自定义显示动画，第一项是模态框，第二项是背景</td>
<td>Array</td>
<td>[&#39;ease&#39;, &#39;fade&#39;]</td>
</tr>
<tr>
<td>transfer</td>
<td>是否将弹层放置于 body 内</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>position</td>
<td>纵向位置可取值(&quot;center&quot;和数值)</td>
<td>string/number</td>
<td>&quot;center&quot;</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-modalevents1_2" class="vue-doc-element">Modal events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-ok</td>
<td>点击确定的回调</td>
<td>无</td>
</tr>
<tr>
<td>on-cancel</td>
<td>点击取消的回调</td>
<td>无</td>
</tr>
<tr>
<td>on-visible-change</td>
<td>显示状态发生变化时触发</td>
<td>true / false</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-modalslot1_3" class="vue-doc-element">Modal slot</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>header</td>
<td>自定义页头</td>
</tr>
<tr>
<td>footer</td>
<td>自定义页脚内容</td>
</tr>
<tr>
<td>close</td>
<td>自定义右上角关闭内容</td>
</tr>
<tr>
<td>无</td>
<td>对话框主体内容</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-modalinstance1_4" class="vue-doc-element">Modal instance</h3>
<p class="vue-doc-element">通过直接调用以下方法来使用：</p>
<ul class="vue-doc-element">
<li><code class="vue-doc-element">this.$Modal.info(config)</code></li>
<li><code class="vue-doc-element">this.$Modal.success(config)</code></li>
<li><code class="vue-doc-element">this.$Modal.warning(config)</code></li>
<li><code class="vue-doc-element">this.$Modal.error(config)</code></li>
<li><code class="vue-doc-element">this.$Modal.confirm(config)</code></li>
<li><code class="vue-doc-element">this.$Modal.remove()//全局关闭对话框的方法</code></li>
</ul>
<p class="vue-doc-element">以上方法隐式地创建及维护Vue组件。参数 <code class="vue-doc-element">config</code> 为对象，具体说明如下：</p>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题</td>
<td>String / Element String</td>
<td>-</td>
</tr>
<tr>
<td>content</td>
<td>内容</td>
<td>String / Element String</td>
<td>-</td>
</tr>
<tr>
<td>render</td>
<td>自定义内容，使用后不再限制类型， content 也无效。 学习 Render 函数的内容</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>宽度，单位 px</td>
<td>Number / String</td>
<td>416</td>
</tr>
<tr>
<td>okText</td>
<td>确定按钮的文字</td>
<td>String</td>
<td>确定</td>
</tr>
<tr>
<td>cancelText</td>
<td>取消按钮的文字，只在Modal.confirm()下有效</td>
<td>String</td>
<td>取消</td>
</tr>
<tr>
<td>loading</td>
<td>点击确定按钮时，确定按钮是否显示 loading 状态，开启则需手动调用Modal.remove()来关闭对话框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>scrollable</td>
<td>页面是否可以滚动</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>closable</td>
<td>是否可以按 Esc 键关闭</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>onOk</td>
<td>点击确定的回调</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>onCancel</td>
<td>点击取消的回调，只在Modal.confirm()下有效</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>extraBtn</td>
<td>添加按钮</td>
<td>Array</td>
<td>-</td>
</tr>
</tbody>
</table>
<h4 id="vue-doc-extrabtn" class="vue-doc-element">extraBtn</h4>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>name</td>
<td>按钮文字</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>type</td>
<td>按钮类型</td>
<td>String</td>
<td>primary</td>
</tr>
<tr>
<td>onOk</td>
<td>点击回调</td>
<td>Function</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import democustom from './demo/custom.md';
import demoasync from './demo/async.md';
import demodisabled from './demo/disabled.md';
import demovertical from './demo/vertical.md';
import demoinstance from './demo/instance.md';
import democonfirm from './demo/confirm.md';
import demorender from './demo/render.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,democustom,demoasync,demodisabled,demovertical,demoinstance,democonfirm,demorender
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


